:root {
  --primary-bg1: #eeeeee;
  --primary-bg2: #00adff;
  --primary-bg3: #ffffff;
  --primary-text1: #808080;
  --primary-text2: #111111;
  --primary-text3: #444444;
  --secondary-color1: #d5d5d5;
  --secondary-color2: #9b9b9b;
  --secondary-color3: #616161;
  --secondary-color4: #1400c4;
}

* {
  margin: 0;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

body {
  margin: 0;
  overflow: auto;
  scroll-behavior: smooth;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app-main {
  background-color: var(--pbgc);
}

.app {
  padding: 0 7rem;
}

p {
  text-wrap: pretty;
}

/* scollbar width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--primary-bg2);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--secondary-color4);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--primary-bg3);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (max-width: 768px) {
  .app {
    padding: 0;
  }
}
